*, a, button, input {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html, body {
  overflow-x: hidden;
  min-height: 100%;
}

body {
  margin: 0;
  background-color: white;
  padding-bottom: 100px;
}

body.default-theme {
  background-color: white;
}

body.default-theme .demo-theme-button {
  background-color: rgba(255, 255, 255, 0.8);
}

body.default-theme .demo-theme-button .text-switcher__switcher {
  width: 47px;
}

@media screen and (max-height: 600px) {
  body.default-theme .demo-theme-button .text-switcher__switcher {
    width: 38px;
  }
}

body.default-theme .demo-theme-button .text-switcher__switcher span:first-of-type {
  transform: translate(0, 0) scale(1);
  opacity: 1;
}

body.default-theme .demo-theme-button .text-switcher__switcher span:last-of-type {
  transform: translate(0, 24px) scale(0.95);
  opacity: 0;
}

body.dark-theme {
  background-color: #242F3E;
}

body.dark-theme .demo-theme-button {
  background-color: rgba(36, 47, 62, 0.9);
  color: #36A8F1;
}

body.dark-theme .demo-theme-button:active {
  color: #66bdf4;
}

body.dark-theme .demo-theme-button .text-switcher__switcher {
  width: 35px;
}

@media screen and (max-height: 600px) {
  body.dark-theme .demo-theme-button .text-switcher__switcher {
    width: 28px;
  }
}

body.dark-theme .demo-theme-button .text-switcher__switcher span:first-of-type {
  transform: translate(0, -24px) scale(0.95);
  opacity: 0;
}

body.dark-theme .demo-theme-button .text-switcher__switcher span:last-of-type {
  transform: translate(0, 0) scale(1);
  opacity: 1;
}

.demo-theme-button {
  color: #108BE3;
  background: rgba(255, 255, 255, 0.9);
  display: block;
  padding: 16px 24px;
  border-radius: 50px;
  text-align: center;
  border: 0;
  font-size: 20px;
  outline: none;
  cursor: pointer;
  transition: opacity 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98), color 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98);
  position: fixed;
  z-index: 1000;
  bottom: 16px;
  left: 50%;
  transform: translate(-50%, 0);
  user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  margin: 0;
}

.demo-theme-button:active {
  color: #35a3f1;
}

@media screen and (max-width: 768px) {
  .demo-theme-button {
    cursor: default;
    width: 100%;
    padding: 24px 16px 28px;
    bottom: 0;
    border-radius: 0;
    left: 0;
    text-align: center;
    transform: translate(0, 0);
  }
}

@media screen and (max-height: 600px) {
  .demo-theme-button {
    cursor: default;
    width: 100%;
    padding: 12px 16px 16px !important;
    font-size: 16px;
    bottom: 0;
    border-radius: 0;
    left: 0;
    text-align: center;
    transform: translate(0, 0);
  }
}

.text-switcher {
  white-space: nowrap;
}

.text-switcher > span {
  display: inline-block;
  vertical-align: top;
}

.text-switcher__switcher {
  display: inline-block;
  position: relative;
  transition: width 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98);
  will-change: width;
}

.text-switcher__switcher span {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98), opacity 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98);
  will-change: transform;
  transform-origin: 50% 50%;
}

@media screen and (min-width: 768px) {
  .telechart2-root {
    box-shadow: inset 0 0 0 1px rgba(174, 174, 174, 0.2);
    padding-bottom: 8px;
  }
}
