.telechart2-root {
  display: block;
  position: relative;
}

.telechart2-root,
.telechart2-root * {
  font-family: 'Roboto', Arial, sans-serif;
}

.grabbable {
  cursor: move;
  /* fallback if grab cursor is unsupported */
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
}

.grabbing {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

.telechart2-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  padding: 20px 12px 0;
  color: black;
}

.telechart2-header .telechart2-root_theme_dark.telechart2-header,
.telechart2-root_theme_dark .telechart2-header {
  color: white;
}

@media screen and (max-width: 768px) {
  .telechart2-header {
    user-select: none;
  }
}

.telechart2-title {
  font-weight: 600;
  font-size: 16px;
}

.telechart2-main {
  display: block;
  position: relative;
}

.telechart2-series-canvas {
  display: block;
}

.telechart2-axis-canvas {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.telechart2-ui-canvas {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.telechart2-navigation {
  display: block;
  position: relative;
  height: 44px;
}

.telechart2-navigation-series-canvas {
  display: block;
  position: absolute;
  width: 100%;
  top: 2px;
  left: 0;
  margin: 0 12px;
  border-radius: 8px;
}

.telechart2-navigation-ui-canvas {
  display: block;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.telechart2-label-buttons {
  padding: 14px 12px 0;
  opacity: 1 !important;
  transition: opacity 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98);
}

@keyframes shaking {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }
  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}

.telechart2-label-button {
  padding: 4px 19.5px;
  border: 2px solid transparent;
  background-color: transparent;
  outline: none;
  border-radius: 40px;
  color: white;
  transition: transform 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98), opacity 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98);
  will-change: transform;
  user-select: none;
  opacity: 1;
  transform: scale(1);
  white-space: nowrap;
}

.telechart2-label-button:not(:last-of-type) {
  margin-right: 6px;
  margin-bottom: 7px;
}

@media screen and (min-width: 768px) {
  .telechart2-label-button {
    cursor: pointer;
  }
}

.telechart2-label-button__icon {
  border-radius: 30px;
  display: inline-block;
  vertical-align: middle;
  transform: scale(1.1);
  width: 0;
}

.telechart2-label-button__icon path {
  stroke-dasharray: 15;
  stroke-dashoffset: 15;
}

.telechart2-label-button__text {
  display: inline-block;
  vertical-align: middle;
  margin-left: 1px;
}

.telechart2-label-button_selected {
  padding: 4px 12px 4px 5px;
}

.telechart2-label-button_selected .telechart2-label-button__icon {
  width: 22px;
}

.telechart2-label-button_selected .telechart2-label-button__icon path {
  stroke-dashoffset: 0 !important;
}

.telechart2-label-button_shaking {
  animation-name: shaking;
  animation-duration: 500ms;
}

.telechart2-label-button_creating {
  opacity: 0;
  transform: scale(0.8);
}

.telechart2-chart-label {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  padding: 8px 10px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 2px -1px rgba(0, 0, 0, 0.43), 0 1px 3px 0px rgba(0, 0, 0, 0.13);
  transition: opacity 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98);
  pointer-events: none;
  user-select: none;
  opacity: 0;
  min-width: 140px;
  max-width: 170px;
}

.telechart2-chart-label .telechart2-root_theme_default.telechart2-chart-label,
.telechart2-root_theme_default .telechart2-chart-label {
  background-color: #fff;
}

.telechart2-chart-label .telechart2-root_theme_dark.telechart2-chart-label,
.telechart2-root_theme_dark .telechart2-chart-label {
  background-color: #1c2533;
  color: white;
  box-shadow: none;
}

@media screen and (min-width: 768px) {
  .telechart2-chart-label {
    cursor: pointer;
  }
}

.telechart2-chart-label_visible {
  opacity: 1;
  pointer-events: auto !important;
}

.telechart2-chart-label__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.telechart2-chart-label__header-arrow {
  width: 20px;
  height: 20px;
  background: url(data:image/svg+xml;base64,) no-repeat center center;
  background-size: 100%;
  position: relative;
  right: -4px;
}

.telechart2-chart-label__date {
  font-size: 13px;
  font-weight: bold;
}

.telechart2-chart-label__table {
  overflow: hidden;
  margin-top: 4px;
}

.telechart2-chart-label__table-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-top: 4px;
  font-size: 13px;
}

.telechart2-chart-label__table-item:first-of-type {
  margin-top: 0px;
}

.telechart2-chart-label__table-item-title-wrapper {
  display: flex;
  flex-direction: row;
}

.telechart2-chart-label__table-item-percentage {
  width: 24px;
  text-align: right;
  font-weight: bold;
}

.telechart2-chart-label__table-item-value {
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-left: 4px;
}

